import { request } from '../request';
import { getErrorMsg } from '../interfaceRules/errorMsg';

/**
 * todo 0H5
 * @param { string } albumId id
 */
export function apiZeroBuy(albumId) {
  return request({
    method: 'GET',
    url: `/h5/order/zerobuy/${albumId}`
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( ': ', err );
  })
}

