/**
 * todo H5!
 * @param { string } province 
 * @param { string } city 
 * @param { string } area 
 * @param { string } address 
 * @param { string } consigneeMobileNo 
 * @param { string } consignee 
 * @param { string } zipcode 	
 * @param { string } leaveMessage 
 * @param { array } spus  SpuItem
 *    @param { string } spuId id
 *    @param { string } skuId id
 *    @param { string } quantity 
 *    @param { string } spuName 
 */
export function apiOrderSubmit({ province = '', city = '', area = '', address = '', consigneeMobileNo = '', consignee = '', zipcode = '', leaveMessage = '', spus = [] }) {
  return request({
    method: 'POST',
    url: '/h5/order/submit',
    data: {
      province,
      city,
      area,	
      address,
      consigneeMobileNo,
      consignee,
      zipcode,
      leaveMessage,
      spus
    }
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( ': ', err );
  })
}

