import { request } from '../request';
import { getErrorMsg } from '../interfaceRules/errorMsg';
import { getMessageType } from '../constantParams/messageType';
import { PAGE_NUM, PAGE_SIZE } from '../constantParams/defaultPage';

/**
 * todo 
 * @param { String } targetId idtype = '0'  idtype = '2' id
 * @param { String } type '': 0 '': 2album  section '0'  '2'
 * @param {*} albumId id
 * @param {*} content 
 */
export function apiSaveLeaveMessage({ targetId, type, albumId, content }) {
  type = getMessageType( type );

  return request({
    method: 'POST',
    url: '/leavemsg/save',
    data: {
      targetId,
      type,
      albumId,
      content
    }
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( ': ', err );
  })
}

