import { request } from '../request';
import { getErrorMsg } from '../interfaceRules/errorMsg';
import { getMessageType } from '../constantParams/messageType';
import { PAGE_NUM, PAGE_SIZE } from '../constantParams/defaultPage';

/**
 * todo 
 * @param { string } msgId id
 * @param { string } msgUserId 	id
 * @param { string } content 	
 * @param { string } srcCommentId id
 */
export function apiSaveComment({ msgId, msgUserId, content, srcCommentId }) {
  return request({
    method: 'POST',
    url: '/comment/save',
    data: {
      msgId,
      msgUserId,
      content,
      srcCommentId
    }
  }).then(result => {
    if (result.code === '0') {
      return Promise.resolve({
        type: 'success',
        data: result.data
      })
    } else {
      const message = getErrorMsg(result.code);
      return Promise.resolve({
        type: 'fail',
        message: result.message || message
      })
    }
  }).catch(err => {
    console.log( ': ', err );
  })
}

